// Импортируем глобальные переменные
@import 'master';
@import 'svg';
@import 'animations';

html,
body {
  font-family: 'Montserrat', sans-serif;
  height: 100%;
  padding: 0;
}

body {
  display: flex;
  flex-direction: column;
  font-size: 16px;
  color: #000000;
}

.main {
  flex: 1 0 auto;
}

.footer {
  flex-shrink: 0;
}

a {
  color: $color_main;
  transition: 0.2s;
}

section {
  padding: 40px 0;
}

.text-color {
  &-main {
    color: $color_main
  }

  &-white {
    color: #fff;
  }

  &-black {
    color: #000;
  }
}

.video-player-rounded {
  border-radius: 24px;
}

.text-size-xs {
  font-size: 0.85em;
}

.overflow-none {
  overflow: hidden;
}

.underline-none {
  text-decoration: none !important;
}

.header {
  position: absolute;
  width: 100%;
  left: 0;
  top: 0;
  z-index: 10;

  padding: 15px 0;

  &__logo {
    width: 140px;
  }

  &__nav-main {
    text-align: center;

    a {
      color: #000000;
      font-size: 1.25em;
      transition: 0.2s;

      &:hover,
      &.active {
        color: $color_main
      }
    }
  }

  &.floating {
    background: lighten($color_main, 32%);
    position: fixed;
    width: 100%;
    left: 0;
    top: 0;
    animation: fadeIn 0.5s ease-in both;
  }
}

.contacts {
  display: flex;
  flex-direction: column;

  i {
    font-size: 20px;
  }
}

.section-title {
  &-wrapper {
    position: relative;
    padding-left: 6em;

    &::after {
      content: '';
      position: absolute;
      width: 5em;
      height: 5em;
      border: 0.75em solid $color_main;
      border-radius: 50%;
      top: 0.75em;
      left: 0;
    }
  }

  &--bold {
    font-weight: bold;
    font-size: 1.65em;
  }

  &--lane {
    padding-left: 2.25em;
    position: relative;

    &::after {
      position: absolute;
      content: '';
      top: 0.75em;
      left: 0;
      width: 1.65em;
      height: 2px;
      background-color: $color_main;
      border-radius: 4px;
    }
  }

  font-size: 1.5em;
}

.section-hero {
  padding-top: 140px;
  background-image: url('../images/hero.png');
  background-repeat: no-repeat;
  background-size: 60%;
  background-position: center left;
  // background-color: rgba(0,0,0,0.12);
  min-height: 100vh;
  position: relative;

  &__overlay {
    background-color: #000;
    opacity: 0.12;
    position: absolute;
    top: 0;
    left: 0;
    height: 100%;
    width: 100%;
  }
}

.hero-form {
  z-index: 1;
  border: 10px solid $color_main;
  border-radius: 48px;
  padding: 40px;
  text-align: center;
  position: relative;
  max-width: 540px;

  &__title {
    font-size: 1.35em;
  }

  &__promo-label {
    position: absolute;
    top: 25px;
    right: 40px;
    font-size: 1.15em;

    &-original {
      text-decoration: line-through;
    }

    &-discounted {
      color: $color_main;
      font-weight: bold;
    }
  }

  &__hint {
    font-size: 0.75em;
  }

  &__cta {
    text-transform: uppercase;
    text-decoration: underline;
    color: #000000;
    opacity: 0.65;

    &:hover {
      color: #000000;
      opacity: 1;
    }
  }

  &__control {
    &-wrapper {
      position: relative;
      width: 100%;
    }

    &-icon {
      position: absolute;
      top: 4px;
      left: 4px;
      opacity: 0.7;
    }

    border: none;
    background: none;
    border-bottom: 2px solid $color_main;
    padding-left: 24px;
    outline: none;
    width: 100%;
  }

  &__button {
    &-wrapper {
      position: relative;

      &::after {
        position: absolute;
        top: 28px;
        right: -20px;
        content: ' ';
        width: 40px;
        height: 2px;
        border-radius: 4px;
        background-color: #000;
      }
    }

    padding: 16px 34px;
    background-color: $color_main;
    color: #000;
    border-radius: 26px;
    border: none;
    font-weight: bold;
    outline: none;
    transition: 0.4s;

    &:hover {
      opacity: 0.8;
    }
  }
}

.quotation {
  position: relative;
  padding-left: 2.25em;

  &::before {
    position: absolute;
    content: '"';
    font-weight: bold;
    font-size: 5em;
    top: -0.4em;
    left: 0;
  }

  &::after {
    position: absolute;
    content: '';
    bottom: -10px;
    left: 2.25em;
    width: 80px;
    height: 4px;
    background-color: #000;
    border-radius: 4px;
  }
}

.circles-list {
  list-style: none;
  ;
  padding: 0;
  margin: 0;
  font-size: 1em;
  counter-reset: circle-counter;

  &__item {
    position: relative;
    padding-left: 5.5em;
    margin-bottom: 25px;
    counter-increment: circle-counter;

    &:before {
      content: counter(circle-counter);
      font-size: 2em;
      position: absolute;
      font-weight: bold;
      border: 0.35em solid #05dcfc;
      border-radius: 50%;
      top: 0.55em;
      left: 0;
      padding: 0 0.525em;
    }
  }
}

.section-solution {
  background-image: url('../images/background_1.png');
  background-repeat: no-repeat;
  background-size: 101%; // removes ghost border
  background-position: top center;
  background-color: rgba(0, 0, 0, .954);
  color: #fff;
}

.footer {
  padding: 40px 0;
  background-image: url('../images/background_3.png');
  background-repeat: no-repeat;
  background-size: 101%; // removes ghost border
  background-position: top center;
  background-color: rgba(0, 0, 0, .954);
  color: #fff;
}

.map-wrapper {
  position: relative;

  .map-overlay {
    background-color: rgba(0, 0, 0, 0.45);
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;

    &__content {
      position: absolute;
      top: 50%;
      left: 50%;
      transform: translate(-50%, -50%) rotate(-45deg);
      text-align: center;
      font-size: 2em;
      width: 100%;
    }
  }
}

.contact-info {
  font-size: 1.25em;
}

#backToTop {
  float: right;
  background-color: $color_main;
  color: #000;
  padding: 10px 18px;
  border-radius: 50%;
  font-size: 24px;
  transition: 0.2s;

  &:hover {
    opacity: 0.75;
  }
}

.cdk-overlay {
  display: none;
  position: fixed;
  z-index: 100;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background: rgba(0, 0, 0, 0.36);
  justify-content: center;
  align-items: center;

  &.active {
    display: flex;
  }
}

.dialog {
  &-wrapper {
    background: #fff;
    border-radius: 48px;
    max-width: 540px;
    text-align: center;
    padding: 1.5em 5em;
    max-height: 80%;
  }

  overflow: auto;
  height: 100%;

  &__form {
    text-align: center;
    padding: 0 3em;

    &-control {
      width: 100%;
      display: block;
      background: none;
      border: 2px solid #000;
      border-radius: 28px;
      outline: none;
      padding: 0.75em 1.5em;
    }

    &-button {
      width: 100%;
      padding: 1em 2em;
      background-color: $color_main;
      color: #fff;
      border-radius: 26px;
      border: none;
      font-weight: bold;
      outline: none;
      transition: 0.4s;

      &:hover {
        opacity: 0.8;
      }
    }
  }

  &__title {
    font-size: 2em;
    font-weight: bold;
  }

  &__hint {
    font-size: 0.85em;
    opacity: 0.85;
  }
}

.solution {
  padding: 24px 28px;
  border: 1px solid #fff;
  border-radius: 50%;
  position: relative;
  display: inline-block;
  margin-bottom: 1em;
  &__circle{
    &::after {
      content: '';
      position: absolute;
      width: 2.5em;
      height: 2.5em;
      border: 0.25em solid $color_main;
      border-radius: 50%;
    }
    &--bottom-right {
      &::after {
        bottom: -0.35em;
        right: -2em;
      }
    }
    &--bottom-left {
      &::after {
        bottom: -0.35em;
        left: -2em;
      }
    }
    &--top-right {
      &::after {
        top: -0.35em;
        right: -2em;
      }
    }
    &--top-left {
      &::after {
        top: -0.35em;
        left: -2em;
      }
    }
  }
}

.section-button {
  position: relative;
  padding: 1em 2em;
  background-color: rgba($color_main, .1);
  color: #fff;
  border-radius: 26px;
  border: 1px solid $color_main;
  font-weight: bold;
  outline: none;
  transition: 0.4s;
  &::after {
    position: absolute;
    top: 28px;
    right: -20px;
    content: ' ';
    width: 40px;
    height: 2px;
    background-color: #fff;
    border-radius: 4px;
  }
  &:hover {
    background-color: $color_main;
  }

  &--dark {
    color: #000;
    &::after {
      background-color: #000;
    }
    &:hover {
      color: #fff;
    }
  }
}

.section-subscription {
  background-image: url('../images/background_2.png');
  background-repeat: no-repeat;
  background-size: 101%; // removes ghost border
  background-position: top center;
  background-color: rgba(0, 0, 0, .954);
  color: #fff;
}

.section-plan {
  background-image: url('../images/plan_1.png'), url('../images/plan_2.png');
  background-position: left bottom, right center;
  background-size: 375px, 275px;
  background-repeat: no-repeat;
}

.plan-steps {
  display: flex;
  flex-wrap: wrap;
  justify-content: flex-end;
  &__item {
    position: relative;
    border: 8px solid $color_main;
    border-radius: 4px;
    padding: 0.75em 1.75em;
    font-size: 1.5em;
    font-weight: bold;
    display: flex;
    align-items: center;
    margin-right: 2em;
    margin-bottom: 2em;
    &-text {
      width: 175px;
    }
    &-digit {
      position: absolute;
      bottom: -22px;
      right: 30px;
      background: #fff;
      padding: 0 28px;
      color: $color_main;
    }
    &::before {
      position: absolute;
      content: '';
      height: 4px;
      width: 40px;
      top: 50%;
      left: -30px;
      background: #000;
    }
  }
}

.subscription-card {
  text-align: center;
  background-color: #fff;
  color: #000;
  padding: 2em 3em;
  border: 0.35em solid transparent;
  position: relative;
  transition: 0.4s;
  margin-bottom: 20px;
  &__tier {
    font-size: 4em;
    color: $color_main;
    font-weight: bold;
  }
  &__title {
    font-size: 1.25em;
    font-weight: bold;
  }
  &__list {
    text-align: left;
    list-style: none;
    &-item {
      &::before {
        content: "\2022"; 
        color: $color_main;
        font-weight: bold;
        display: inline-block; 
        width: 1em;
        margin-left: -1em;
      }
    }
  }
  &__price {
    font-weight: bold;
    font-size: 1.5em;
    color: $color_main;
  }
  &__promo-label {
    position: absolute;
    content: '';
    top: 10px;
    right: 10px;
    display: flex;
    justify-content: center;
    align-items: center;
    transform: rotate(-45deg);
    background: $color_main;
    color: #fff;
    width: 90px;
    height: 90px;
    padding: 20px;
    text-align: center;
    border-radius: 50%;
    font-weight: bold;
    font-size: 0.75em;
  }

  &:hover {
    border-color: $color_main;
  }
}

.gallery-image {
  border-radius: 28px;
  box-shadow: 0 6px 6px 0 rgba(0, 0, 0, 0.16);
}